	module.exports = {"data":{"post":{"html":"<p>I'm sure there is news occuring somewhere in the world today, but you'd never know it watching televsion here in Germany. On ZDF, for example, over half the evening news plus an hour-long special report is devoted to Germany reaching the World Cup Final with a 1-0 win over South Korea. No news event (other than 9-11) would receive this much coverage.</p>\n<p>That must really get on the nerves of those Germans who do not care at all about sports.</p>","fields":{"title":"Stop The World! We're In The Finals!","date":"25 Jun 2002","url":"https://papascott.de/2002-06-25-stop-the-world-were-in-the-finals/"}}},"pathContext":{"slug":"/2002-06-25-stop-the-world-were-in-the-finals/","prev":{"fields":{"title":"Blogcheckup.de","date":"25 Jun 2002","slug":"/2002-06-25-blogcheckupde/"}},"next":{"fields":{"title":"You Can See Me Now Or You Can See Me Later","date":"25 Jun 2002","slug":"/2002-06-25-you-can-see-me-now-or-you-can-see-me-later/"}}}}

